import React, { useState, useEffect} from 'react'
import { Button, Col, Form, Row } from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
import {Link} from 'react-router-dom'
import {login} from '../actions/userAction'
import {addAlert} from '../actions/alertAction'
import FormContainer from '../components/FormContainer'


const LoginScreen = ({location, history}) => {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const redirect = location.search ? location.search.split('=')[1] : '/'

    const dispatch = useDispatch()
    const userLogin = useSelector(state => state.userLogin)
    const {userInfo, error, loading} = userLogin

    useEffect(() => {
        if(userInfo){
            history.push(redirect)
        }
        if(error){
            dispatch(addAlert(error, 'danger'))
        }
    }, [dispatch, history, userInfo, redirect, error])

    const submitHandler = (e) => {
        e.preventDefault();
        if(email && password){
            dispatch(login(email, password))
            setEmail('')
            setPassword('')
        }else{
            dispatch(addAlert('test error message', 'danger'))
        }
    }

    return (
        <FormContainer>
            <h1>Sign In</h1>
            <Form onSubmit={submitHandler}>
                <Form.Group>
                    <Form.Label>Email address</Form.Label>
                    <Form.Control 
                        type="email" 
                        placeholder="Enter Email" 
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                </Form.Group>
                <Form.Group>
                    <Form.Label>Password</Form.Label>
                    <Form.Control 
                        type="password" 
                        placeholder="Enter Password" 
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                </Form.Group>
                <Button type='submit' variant='primary' disabled={loading}>{loading ? 'loading...' : 'Sign In'}</Button>
            </Form>
            <Row className='py-3'>
                <Col>New Customer? <Link to={redirect ? `/register?redirect=${redirect}` : `/register`}>Register</Link></Col>
            </Row>
        </FormContainer>
    )
}

export default LoginScreen
